@import url('https://fonts.googleapis.com/css2?family=Nunito+Sans:wght@200;300;400;800;900&display=swap');
html, body {
    font-family: 'Nunito Sans', sans-serif;
}

.MuiFab-primary {
    background-color: #6a53e4 !important;
}

.MuiFab-root {
    position: absolute !important;
    right: 50px;
    bottom: 30px;
}

.scroll-container-hidden-bars {
    overflow: scroll;
    -ms-overflow-style: none;  /* IE and Edge */
    scrollbar-width: none;  /* Firefox */
}

/* chrome */
.scroll-container-hidden-bars::-webkit-scrollbar {
    display: none;
}